/**
 * Uses the browser's URL constructor to extract parts of a URL, plus
 * the domain parsed from a hostname that has more than one level
 * * NOTE: If using to extract on many URLs, a RegEx is faster:
 * * https://stackoverflow.com/a/8498629
 * @param   {String} url The URL to parse
 * @returns {Object} All properties on the URL interface
 */
const extractUrlParts = (url) => {
	if (!url) {
		return null;
	}

	const urlParts = new URL(url);
	if (urlParts.hostname) {
		urlParts.domain = urlParts.hostname.substring(
			urlParts.hostname.lastIndexOf(
				'.',
				urlParts.hostname.lastIndexOf('.') - 1
			) + 1
		);
	}
	return urlParts;
};

export default extractUrlParts;
