import extractUrlParts from 'utils/extractUrlParts';
import { formatOccupationSnippet } from 'workers/processExtractedData/utils/occupation';
import {
	getMemberIdFromUrn,
	getIdFromProfileUrl,
	getSalesNavIdFromUrn,
} from 'workers/processExtractedData/utils/identifiers';
import createSeamlessContact from 'workers/processExtractedData/utils/createSeamlessContact';
import isValidContact from 'utils/isValidContact';

const parseStandardBlendedSearchContact = (item) => {
	const {
		entityUrn,
		image: profileImg,
		navigationUrl,
		primarySubtitle: occupationPrimarySubtitle,
		summary: possibleOccupationSnippetText,
		secondarySubtitle,
		title,
		trackingUrn,
	} = item;

	const parsedProfileUrl = navigationUrl && extractUrlParts(navigationUrl);

	const flagshipProfileUrl = parsedProfileUrl
		? `${parsedProfileUrl.origin}${parsedProfileUrl.pathname}`
		: navigationUrl;

	let occupationFromCurrentSnippet;
	if (possibleOccupationSnippetText?.text?.startsWith('Current:')) {
		occupationFromCurrentSnippet = formatOccupationSnippet(
			possibleOccupationSnippetText.text
		);
	}

	const contactOpts = {
		liMemberId: trackingUrn && getMemberIdFromUrn(trackingUrn),
		publicIdentifier:
			navigationUrl &&
			getIdFromProfileUrl(navigationUrl, 'standardBlendedSearchContact'),
		salesNavIdentifier: entityUrn && getSalesNavIdFromUrn(entityUrn),
		flagshipProfileUrl,
		fullName: title?.text,
		profilePicture:
			profileImg?.attributes?.[0]?.detailDataUnion
				?.nonEntityProfilePicture?.vectorImage,
		location: secondarySubtitle?.text,
		occupation:
			occupationFromCurrentSnippet || occupationPrimarySubtitle?.text,
	};

	const parsedContact = createSeamlessContact(contactOpts);
	parsedContact.pageSectionSourced = 'standardBlendedSearchContact';
	parsedContact.itemType = 'contact';
	return isValidContact(parsedContact) ? parsedContact : null;
};

export default parseStandardBlendedSearchContact;
