import { expose } from 'comlink';
import parseStandardBlendedSearchCompany from 'workers/processExtractedData/processStandardBlendedSearchData/parseStandardBlendedSearchCompany';
import parseStandardBlendedSearchContact from 'workers/processExtractedData/processStandardBlendedSearchData/parseStandardBlendedSearchContact';

const COMPANY_TRACKING_URN_INCLUDES_TEXT = ':company:';
const CONTACT_ENTITY_URN_INCLUDES_TEXT = 'fsd_profile';

function hasStandardBlendedSearchCompanyData(data) {
	return data.included?.some?.((item) =>
		item?.trackingUrn?.includes(COMPANY_TRACKING_URN_INCLUDES_TEXT)
	);
}

function hasStandardBlendedSearchContactData(data) {
	return data.included?.some?.((item) =>
		item?.entityUrn?.includes(CONTACT_ENTITY_URN_INCLUDES_TEXT)
	);
}

const processStandardBlendedSearchData = async (pageData) => {
	let parsedItems = [];

	if (hasStandardBlendedSearchCompanyData(pageData)) {
		const companyItems = pageData.included.filter((item) =>
			item.trackingUrn?.includes(COMPANY_TRACKING_URN_INCLUDES_TEXT)
		);
		const companyParsers = companyItems.map((item) =>
			parseStandardBlendedSearchCompany(item)
		);
		if (companyParsers?.length) {
			const parsedCompanyItems = await Promise.all(companyParsers);
			parsedItems = [...parsedItems, ...parsedCompanyItems];
		}
	}

	if (hasStandardBlendedSearchContactData(pageData)) {
		const contactItems = pageData.included.filter((item) =>
			item.entityUrn.includes(CONTACT_ENTITY_URN_INCLUDES_TEXT)
		);
		const contactParsers = contactItems.map((item) =>
			parseStandardBlendedSearchContact(item)
		);
		if (contactParsers?.length) {
			const parsedContactItems = await Promise.all(contactParsers);
			parsedItems = [...parsedItems, ...parsedContactItems];
		}
	}

	return {
		pagingData: pageData.data?.paging,
		parsedItems,
	};
};

expose(processStandardBlendedSearchData, self);
