import createSeamlessCompany from 'workers/processExtractedData/utils/createSeamlessCompany';
import {
	getMemberIdFromUrn,
	getIdFromProfileUrl,
} from 'workers/processExtractedData/utils/identifiers';
import isValidCompany from 'utils/isValidCompany';

const parseStandardBlendedSearchCompany = (item) => {
	const {
		entityUrn,
		image,
		navigationUrl,
		primarySubtitle,
		secondarySubtitle,
		summary,
		title,
	} = item;

	const linkedInId = entityUrn && getMemberIdFromUrn(entityUrn);

	let industry;
	let location;
	if (primarySubtitle?.text?.includes(' • ')) {
		const subtitleParts = primarySubtitle.text.split(' • ');
		if (subtitleParts && subtitleParts.length) {
			[industry, location] = subtitleParts;
		}
	}

	const logo =
		image?.attributes?.[0]?.detailDataUnion?.nonEntityCompanyLogo
			?.vectorImage;

	const companyOpts = {
		// LinkedIn Identifiers
		linkedInId,
		linkedInProfileIdentifier:
			navigationUrl &&
			getIdFromProfileUrl(navigationUrl, 'standardBlendedSearchCompany'),
		linkedInUrn: entityUrn,

		// Entity info
		name: title?.text ?? null,
		logo,
		description: summary?.text ?? null,

		// Company info
		industries: industry ? [industry] : null,

		// Company contact info
		locationFullString: location,

		// Social
		linkedInFollowerCount:
			secondarySubtitle?.text &&
			secondarySubtitle.text.replace(/\D/g, ''),
	};

	const parsedCompany = createSeamlessCompany(companyOpts);
	parsedCompany.pageSectionSourced = 'standardBlendedSearchCompany';
	parsedCompany.itemType = 'company';
	return isValidCompany(parsedCompany) ? parsedCompany : null;
};

export default parseStandardBlendedSearchCompany;
